import * as React from "react";
import {
  ComposableMap,
  ZoomableGroup,
  Geographies,
  Geography,
  Markers,
  Marker,
} from "react-simple-maps"
import './Canvas.scss'

const wrapperStyles = {
  width: "100%",
  maxWidth: 980,
  margin: "0 auto",
}

class Canvas extends React.Component<any, any> {
  constructor(props) {
    super(props)
    this.state = {
      center: [0,20],
      zoom: 1,
      velocity: 9999999,
      altitude: 9999999,
      cities: [
        { name: "ISS", coordinates: [999999,9999999] }
      ]
    }
    this.handleCitySelection = this.handleCitySelection.bind(this)
    this.handleReset = this.handleReset.bind(this)
  }


  public handleCitySelection(evt) {
    const cityId = evt.target.getAttribute("data-city")
    const city = this.state.cities[cityId]
    this.setState({
      center: city.coordinates,
      zoom: 2,
    })
  }
  public handleReset() {
    this.setState({
      center: [0,20],
      zoom: 1,
    })
  }

  public getCoords(): void {
    fetch(encodeURI('https://api.wheretheiss.at/v1/satellites/25544'))
    .then(res => res.json())
    .then(res => {
      this.setState(prevState => {
        return Object.assign({}, prevState, {
          altitude: parseFloat(res.altitude).toFixed(2),
          velocity: parseFloat(res.velocity).toFixed(2),
          cities: [
            {
              name: "ISS",
              coordinates: [parseFloat(res.longitude).toFixed(4), parseFloat(res.latitude).toFixed(4)]
            }
          ]
        });
      });
    })
  }

  public componentDidMount(): void {
    this.getCoords();
    setInterval(() => {
        this.getCoords();
        console.log(this.state.cities[0].coordinates)
    }, 3500);

  };

  render(): JSX.Element {
    return (
      <div>
        <div>
          <h3>ISS Data - <small>Updated every 3 seconds</small></h3>
          <p>Latitude: {this.state.cities[0].coordinates[1]} - Longitude: {this.state.cities[0].coordinates[0]}</p>
          <p>Orbital velocity: {this.state.velocity}km/h</p>
          <p>Orbital altitude: {this.state.altitude}km</p>
        </div>
        <div style={wrapperStyles}>
          {
            this.state.cities.map((city, i) => (
              <button
                className="MapControlsButton"
                key={i}
                data-city={i}
                onClick={this.handleCitySelection}
                >
                { city.name }
              </button>
            ))
          }
          <button className="MapControlsButton" onClick={this.handleReset}>
            { "Reset" }
          </button>
        </div>
        <div style={wrapperStyles}>
          <ComposableMap
            projectionConfig={{
              scale: 205,
            }}
            width={980}
            height={551}
            style={{
              width: "100%",
              height: "auto",
            }}
            >
            <ZoomableGroup center={this.state.center} zoom={this.state.zoom}>
              <Geographies geography={require('./drawMap.json')}>
                {(geographies, projection) => geographies.map((geography, i) => geography.id !== "ATA" && (
                  <Geography
                    key={i}
                    geography={geography}
                    projection={projection}
                    style={{
                      default: {
                        fill: "#0D0D0D",
                        stroke: "#3C6879",
                        strokeWidth: 1,
                        outline: "none",
                      },
                      hover: {
                        fill: "#607D8B",
                        stroke: "#607D8B",
                        strokeWidth: 0.75,
                        outline: "none",
                      },
                      pressed: {
                        fill: "#FF5722",
                        stroke: "#607D8B",
                        strokeWidth: 0.75,
                        outline: "none",
                      },
                    }}
                  />
                ))}
              </Geographies>
              <Markers>
                {
                  this.state.cities.map((city, i) => (
                    <Marker key={i} marker={city}>
                      <circle
                        cx={0}
                        cy={0}
                        r={6}
                        fill="#FF5722"
                        stroke="#DF3702"
                      />
                    </Marker>
                  ))
                }
              </Markers>
            </ZoomableGroup>
          </ComposableMap>
        </div>
        
      </div>
    )
  }
}

export default Canvas;